@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");

* {
  font-family: "Inter", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

 {
  /* ////////////  About    /////////////////// */
}

.cmp {
  color: #fff;
  font-weight: 400;
  background: #b1845e;
  border-radius: 7px;
  padding: 0.1rem 0.3rem;
}

.mainAbout {
  opacity: 1;
  display: block;
  top: 6rem;
  position: absolute;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
  color: #292929;
  width: 100%;
  margin: 0 auto;
  padding: 0 5rem;
  font-weight: 600;
  text-align: center;
}

.mainAbout h2 {
  text-align: center;
  font-weight: 600;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.mainAbout img {
  max-width: 250px;
  height: auto;
  margin-bottom: 1rem;
}

.mainAbout p {
  font-size: 1.4rem;
  color: #160300;
  font-weight: 400;
  margin-bottom: 5rem;
  text-align: justify;
}

.mainAbout h5 {
  text-align: center;
  font-weight: 600;
  font-size: 5vw;
  font-weight: 700;
  margin-bottom: 2rem;
}

.lineSP {
  background-color: #160300;
  height: 0.2rem;
  content: "";
  border-radius: 40px;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

 {
  /* ////////////  Working Experience    /////////////////// */
}

.exp {
  align-items: center;
}

.exp h2 {
  margin-bottom: 20px;
  font-size: 25px;
  font-weight: 700;
}
.exp h3 {
  font-weight: 700;
  font-size: 22px;
  margin-bottom: 40px;
  text-align: left;
}

.exp span {
  font-weight: 400;
  font-size: 22px;
}

 {
  /* ////////////  Achievements    /////////////////// */
}

.achiev h1 {
  margin-bottom: 20px;
  margin-top: 90px;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.grid {
  position: relative;
  margin: 0 auto;
  padding: 1em 0 4em;
  list-style: none;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  gap: 2rem;
}

.grid h2,
p {
  text-align: left;
}

/* Common style */
.grid figure {
  position: relative;
  overflow: hidden;
  margin: 11px 1%;
  min-width: 320px;
  max-width: 380px;
  max-height: 260px;
  width: 48%;
  background-image: url(data:image/png;base64,);
  text-align: center;
  cursor: pointer;
}

.grid figure img {
  position: relative;
  display: block;
  min-height: 100%;
  max-width: 100%;
  opacity: 0.8;
}

.grid figure figcaption {
  padding: 2em;
  color: #fff;
  font-size: 1.25em;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

/* Text Effect */

.grid figure h2 {
  word-spacing: -0.15em;
  font-weight: 600;
  font-size: 1.5rem;
}

.grid figure h2 span {
  font-weight: 800;
}

.grid figure h2,
.grid figure p {
  margin: 0;
}

.grid figure p {
  font-size: 14px;
  text-align: left;
}

/* Individual effects */

figure.effect-lily img {
  max-width: none;
  width: -webkit-calc(100% + 50px);
  width: calc(100% + 50px);
  opacity: 0.7;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: translate3d(-40px, 0, 0);
  transform: translate3d(-40px, 0, 0);
}

figure.effect-lily figcaption {
  text-align: left;
}

figure.effect-lily figcaption > div {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 2em;
  width: 100%;
  height: 50%;
}

figure.effect-lily h2,
figure.effect-lily p {
  -webkit-transform: translate3d(0, 40px, 0);
  transform: translate3d(0, 40px, 0);
}

figure.effect-lily h2 {
  -webkit-transition: -webkit-transform 0.35s;
  transition: transform 0.35s;
}

figure.effect-lily p {
  color: rgba(255, 255, 255, 0.8);
  opacity: 0;
  -webkit-transition: opacity 0.2s, -webkit-transform 0.35s;
  transition: opacity 0.2s, transform 0.35s;
}

figure.effect-lily:hover img,
figure.effect-lily:hover p {
  opacity: 1;
}

figure.effect-lily:hover img,
figure.effect-lily:hover h2,
figure.effect-lily:hover p {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

figure.effect-lily:hover p {
  -webkit-transition-delay: 0.05s;
  transition-delay: 0.05s;
  -webkit-transition-duration: 0.35s;
  transition-duration: 0.35s;
}

 {
  /* ////////////  Bottom Area    /////////////////// */
}

.mainAbout button {
  margin-top: 2rem;
  color: #000000;
  border-radius: 1px;
  font-size: 2.9vh;
  font-weight: 600;
  border-color: #010405;
  width: 190px;
  height: 60px;
  background-color: #00000000;
  margin-bottom: 3rem;
  transition: all 300ms ease;
}

.mainAbout button:hover {
  color: #ffffff;
  background: #000000;
  cursor: pointer;
}

.fon a:link {
  text-decoration: none;
}

.fon {
  margin-bottom: 2rem;
}

.mainAbout a:link:hover {
  color: #4f4f4f;
}
.mainAbout a:link {
  font-size: 2rem;
  color: #000000;
}

.botto p {
  font-size: 1rem;
  font-weight: 600;
  margin-top: 2rem;
  text-align: center;
}

@media screen and (max-width: 576px) {
  .mainAbout {
    padding: 0 2rem;
  }

  .mainAbout p {
    font-size: 1rem;
  }

  .exp h3 {
    font-size: 1rem;
  }

  .exp span {
    font-size: 1rem;
  }
}
